body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.hide {
  display: none;
}

form .inputs {
  margin-bottom: 20px;
}

legend {
  font-size: 22px;
  text-align: center;
  width: 100%;
}

.flight {
  border: 1px solid  #ccc;
  padding: 5px;
  margin-bottom: 10px;
  
}

.view1 .left-part {
  width: 80%;
  float: left;
}

.view1 .right-part {
  width: 20%;
  float: left;
}

.view1 {
   > .left-part {
     width: 20%;
     float: left;
  }

  .right-part {
  width: 20%;
  float: left;
}
}

.view2 .left-part {
  width: 20%;
  float: left;
}

.view2 .right-part {
  width: 80%;
  float: left;
}

.infinite-scroll-area {
  border: 1px solid #000;
  padding: 5px;
  max-height: 400px;
  min-height: 400px;
  overflow: auto;
}

.chat {
  background: #fafafa;
  width: 80%;
  padding: 5px;
  margin-bottom: 10px;
}


